	module.exports = {"data":{"markdownRemark":{"html":"<p>There are many example projects created by the React community. Feel free to add your own project. If you add a project, please commit to keeping it up to date with the latest versions of React.</p>\n<ul>\n<li><strong><a href=\"https://github.com/ahfarmer/calculator\">Calculator</a></strong> Implementation of the iOS calculator built in React</li>\n<li><strong><a href=\"https://github.com/ahfarmer/emoji-search\">Emoji Search</a></strong> Simple React app for searching emoji</li>\n<li><strong><a href=\"https://github.com/ReactTraining/react-fundamentals/tree/hosting\">Github Battle App</a></strong> Battle two Github users and see the most popular Github projects for any language.</li>\n<li><strong><a href=\"https://www.fpcomplete.com/user/dschalk/Websockets%20Game%20of%20Score\">Haskell Websockets React Game of Score</a>:</strong> React with a Haskell websockets server.</li>\n<li><strong><a href=\"https://showcase.cloud.global.fujitsu.com\">K5 Showcase</a>:</strong> Sample applications for React.js and Flux.</li>\n<li><strong><a href=\"https://github.com/insin/react-hn\">React Powered Hacker News Client</a></strong> A React &#x26; react-router-powered implementation of Hacker News using its Firebase API.</li>\n<li><strong><a href=\"https://github.com/alik0211/pokedex\">Pokedex</a></strong> The list of Pokémon with live search</li>\n<li><strong><a href=\"https://github.com/skidding/flatris\">Progressive Web Tetris</a></strong> Besides a beautiful, mobile-friendly implementation of Tetris, this project is a playground for integrating and experimenting with web technologies.</li>\n<li><strong><a href=\"https://github.com/Rhymond/product-compare-react\">Product Comparison Page</a></strong> Simple Product Compare page built in React</li>\n<li><strong><a href=\"https://github.com/clintonwoo/hackernews-react-graphql\">Hacker News Clone React/GraphQL</a></strong> Hacker News clone rewritten with universal JavaScript, using React and GraphQL.</li>\n<li><strong><a href=\"https://github.com/reddit/reddit-mobile\">Reddit Mobile</a></strong> Reddit’s mobile platform.</li>\n<li><strong><a href=\"http://bitcoinpriceindex.netlify.com\">Bitcoin Price Index (powered by CoinDesk)</a></strong> Simple bitcoin price index data from CoinDesk API.</li>\n<li><strong><a href=\"https://publicapis.netlify.com/\">Public APIs curated by toddmotto</a></strong> A collective list of public JSON APIs for use in web development.</li>\n</ul>","frontmatter":{"title":"Example Projects","next":null,"prev":null},"fields":{"path":"community/examples.md","slug":"community/examples.html"}}},"pathContext":{"slug":"community/examples.html"}}

